.projects {
    width: 100%;
    padding: 0 0 6rem;
    background: #000;
}

.card-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 40px;
}

.card {
    border: 1px solid #eee;
    color: #eee;
    text-align: center;
    padding: 1rem;
    display: flex;
    flex-direction: column;
}

.card:hover {
    background-color: #141414;
}

.card h3 {
    font-size: 1.4rem;
    padding: 1rem;
}

.card p {
    padding: 10px 0;
}

.bar {
    border-bottom: 1px solid #eee;
    width: 10%;
    margin: 1.5rem auto;
    display: block;
}

.btc {
    font-size: 2rem;
    font-weight: 600;
    padding: 1rem 0;
}

.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    align-self: flex-start;
    margin-top: auto;
}

@media screen and (max-width: 1180px) {
    .card-container {
        max-width: 80%;
        margin: auto;
        grid-template-columns: 1fr;
    }
}